.item{
    font-size: 20px;
    background: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    padding: 20px;
    margin: 5px 0px;
}

.item button{
    margin: 0px 20px;
    border: none;
    background: seagreen;
    width: 40px;
    height: 40px;
    border-radius: 100px;
    color: white;
    outline: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    outline: none !important;
    padding: 0px !important;
}

.add{
    font-size: 20px;
    font-variant: small-caps;
    text-transform: lowercase;
    border: none;
    background: sienna;
    position: fixed !important;
    bottom: 100px !important;
    right: 20px !important;
    color: white;
    padding: 10px 20px;
    border-radius: 100px;
    outline: none !important;
}

.fade-exit{
    opacity: 0.6;
    transform: translateY(30px) translateX(0px) rotateY(0deg) scale(.7);
}

.fade-exit-active{
    opacity: 0;
    transform: translateY(60px) translateX(100px) rotateY(360deg) scale(.5);
    transition: .4s ease-in-out;
}

.fade-enter{
    opacity: .7;
    transform: translateY(-60px) scale(.7);
}

.fade-enter-active{
    opacity: 1;
    transform: translateY(0px) scale(1);
    transition: .4s;
}