@import '../../colors.scss';

.sidebar{
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 70px;
    z-index: 2 !important;
    padding-top: 70px;
    background: $standart;
    height: 100vh;
    transition: .3s;

    @media(max-width:991px){
        left: -300px;
    }

    &_menu{
        list-style: none;
        margin: 0;
        padding: 0;
        z-index: 2 !important;

        li{
            padding: 0 !important;
            display: block;
            width: 100%;
        }

        &_item{
            position: relative;
            font-size: 22px;
            height: 70px;
            display: flex !important;
            padding: 0px 15px !important;
            font-family: Light !important;
            align-items: center !important;
            width: 100%;
            text-decoration: none !important;
            color: white;
            text-transform: lowercase;
            font-variant: small-caps;
            transition: all .3s;
            box-shadow: 0px 1px rgba(255,255,255,0.2) !important;
            border-bottom: 1px solid rgba(0,0,0,0.2) !important;

            
            &::after{
                content: attr(data-tooltip);
                color: white;
                font-size: 16px;
                position: absolute;
                right: 0%;
                background: $standart;
                padding: 5px 10px;
                border-radius: 5px;
                transition: .3s;
                left: calc(100% + 10px);
                width: max-content;
                text-align: left;
                letter-spacing: 1.5px;

                visibility: hidden;
                opacity: 0;
                transform:  scale(0);
                pointer-events: none;
                z-index: -1;
            }

            span{
                display: none;
            }

            &_active{
                background: #fff;
                color: $standart;
            }

            &:hover{
                background: #fff;
                color: $standart;

                &::after{
                    visibility: visible;
                    opacity: 1;
                    transform:  scale(1);
                    pointer-events: all;
                    z-index: auto;
                }

            }

            i,svg{
                margin: 0px 8px;
                padding-top: 3px;
                font-size: 25px !important;
            }
        }
    }

    &_open{
        width: 270px;
        left: 0px;

        .sidebar_menu_item{
            span{
                display: block;
            }

            &:hover{
                &::after{
                    display: none;
                }
            }
        }
    }

    &_toggler{
        position: absolute !important;
        top: 10px !important;
        right: 10px !important;

        @media(min-width: 990px){
            display: none !important;
        }
    }

    &_overlay{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #000000aa;
        z-index: 1;
        display: none;

        @media(min-width: 990px){
            display: none !important;
        }
    }

    &_visible{
        display: block;
    }
}