@import '../../colors.scss';

.header{
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    padding: 0px 20px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    position: fixed;
    top: 0;
    z-index: 1;
    left: 0;
    width: 100%;
    transition: .3s;
    padding-left: 90px;

    @media(max-width:989px){
        padding: 0px 10px !important;
    }

    &_padded{
        padding-left: 290px;
        
        @media(max-width: 989px){
            padding-left: 10px !important;
        }
    }

    &_logo{
        img{
            height: 50px;
        }

        @media(max-width: 400px){
            span{
                display: none;
            }
        }

        font-size: 20px;
        font-family: Extra-bold;
        color: gray;
    }
}


.content{
    padding: 80px 20px;
    transition: .3s;

    @media(max-width: 989px){
        padding: 80px 10px;
    }

    &_padded{
        padding-left: 290px;

        @media(max-width: 989px){
            padding-left: 10px;
        }
    }

    &_hided{
        padding-left: 90px;

        @media(max-width: 989px){
            padding-left: 10px;
        }
    }
}
