@import '../../colors.scss';

.button{
    border-radius: 5px;
    display: inline-flex ;
    align-items: center;
    padding: 7px 10px;
    font-family: Regular;
    outline: none !important;
    font-size: 18px;
    background: $standart;
    text-transform: lowercase;
    letter-spacing: 1px;
    color: white;
    font-variant: small-caps;
    -webkit-box-shadow: 0px 1px 1.5px 1px rgba(0, 0, 0, 0.12);
    box-shadow: 0px 1px 1.5px 1px rgba(0, 0, 0, 0.12);
    border: none;
    transition: .1s;
    justify-content:  center;
    cursor: pointer;

    &:disabled{
        background-color: #999999;
    }

    &_primary{
        background: #4E7DF1;
        &:active{
            background: #225ffc;
        }
    }

    &_danger{
        background:tomato;
    }


    &_block{
        width: 100% !important;
        display: block !important;
    }

    i , span ,svg{
        margin: 0px 3px;
        font-size: 14px !important;
    }

    &_flat{
        border-radius: 100px;
        width: 40px;
        height: 40px;
        padding: 0px !important;
        display: inline-flex;
        align-items: center;
        justify-content: center;

        i,svg,span{
            font-size: 16px !important;
        }
        &::after{
            display: none !important;
        }
    }

    &_transparent{
        background:transparent;
        border: none !important;
        box-shadow: none;
        color: gray;
        
        i,span,svg{
            font-size: 25px !important;
        }
    }
}