.selectList{
    display: inline-flex;
    align-items: flex-start;
    flex-direction: column;
    font-family: 'Regular';
    width: 100%;

    label {
        font-size: 14px;
    }


    select{
        display: block;
        font-size: 16px;
        outline: none !important;
        padding: 5px 15px !important;
        width: 100%;
        border: 1px solid rgb(156, 156, 156);
        background: white;
        cursor: pointer;

        &:focus{
            border: 2px solid lightseagreen;
        }
    }

    p.error{
        font-size: 12px;
        color: tomato;
    }
}