@import '../../mixins.scss';

@include form();


.slidesList{
    background: white;
    padding: 10px;
    position: relative;
    box-shadow: 0px 5px 5px -3px rgba(0,0,0,0.2);
    font-family: 'Regular';
    display: block;
    width: 100%;

    .header{
        grid-template-columns: minmax(50px,100px) 2fr 4fr 1fr 1fr;
        position: sticky !important;
        position: -webkit-sticky !important;
        top: 70px;
        display: grid;
        background: rgb(147, 194, 238);
        box-shadow: 0px 10px 10px rgba(0,0,0,0.2);
        z-index: 1;

        div{
            border-bottom: 1px solid lightgray;
            padding: 10px;
            font-size: 14px;
        }
    }

    .body {
        display: grid;
        grid-template-columns: minmax(50px,100px) 2fr 4fr 1fr 1fr;

        &:hover{
            background-color: rgba(211, 211, 211, 0.329);
        }

        div{
            border-bottom: 1px solid lightgray;
            padding: 5px 10px;
            font-size: 12px;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;

            button{
                height: 20px;
                padding: 0px 10px;
                border: none !important;
                border-radius: 100px;
                color: white;
                background: tomato;
                transition : .2s;
            }

            img {
                width: 30px;
                height: 30px;
                padding: 0px !important;
                border-radius: 1000px;
                box-shadow: 0px 0px 2px 1px rgba(0,0,0,0.2);
                object-fit: cover;
            }
        }
    }
}