.treeselect{
        position: relative !important;
        height: 40px;
        border: 1px solid lightgray;
        font-family: Light;
        height: 40px;
        border-radius: 0px !important;
        cursor: pointer;
        outline: none !important;
        padding: 7px 14px;
        background: white;

        &_data_list{
            position: absolute;
            top: 103%;
            width: 101% !important;
            left: -2px;
            padding: 0px;
            display: none;
            background: white;
            height: auto !important;
            max-height: 300px !important;
            overflow-y: scroll;
            box-shadow: 0px 5px 5px 2px rgba(0,0,0,.3);
            z-index: 1;
        }

        
        
        &:focus{
            border: 2px solid lightseagreen;

            &>.treeselect_data_list{
                display: block;
            }
        }
}

.cascade{
    list-style: none !important;
    padding-left: 20px;

    &_child{
        position: relative !important;
        &_label{
            font-size: 12px !important;
            width: 100% !important;
            height: 40px !important;
            display: flex !important;
            align-items: center !important;
            padding: 0px 20px;
            cursor: pointer;
            padding-left: 30px !important;
            border-bottom: 1px solid lightgray;
            
            &:hover{
                background: #bbbb;
            }
        }
    }
}

.with_child{
    position: relative !important;

    &>div{
        position: relative !important;
        &::before{
            content: "+";
            left: 10px;
            font-size: 14px;
            position: absolute;
        }
    }

    &>ul{
        opacity: 0;
        transition: .7s;
        height: 0px;
        pointer-events: none;
    }
}

.visible{
    opacity: 1 !important;
    pointer-events: all !important;
    height: auto !important;
}