.messageContainer{
    margin: 5px 0px;
    padding: 5px 10px;
    display: none;

    &.visible{
        display: block;
    }

    p{
        margin: 0px !important;
        padding: 0px !important;
    }

    &.success {
        background: rgba(122, 235, 122, 0.726);
        p{
            color: green;
        }
    }

    &.error {
        background: rgba(231, 145, 130, 0.685);
        p{
            color: rgb(92, 0, 0);
        }
    }
}