@mixin form{
    .panel{
        margin: 20px 0px;
        padding-bottom: 15px;
        background-color: white;
        box-shadow: 0px 5px 5px -5px rgba(0,0,0,0.3);
        position: relative;

        &::before , &::after{
            position: absolute;
            content: '';
            font-size: 30px;
            bottom: 15px;
            width: 50%;
            background: rgba(0, 0, 0, 0.541);
            filter: blur(5px);
            display: block;
            z-index: -1;
            height: 20px;
        }

        &::before{
            left: 10px;
            transform: rotate(-5deg);
        }

        &::after{
            right: 10px;
            transform: rotate(5deg);
        }
        h4{
            font-family: 'Bold';
            font-size: 23px;
            text-transform: lowercase;
            padding: 10px 20px;
            border-bottom: 1px solid lightgray;
            font-variant: small-caps;
        }
    
        .form{
            display: grid;
            grid-template-columns: calc(50% - 10px) calc(50% - 10px);
            column-gap: 20px;
            row-gap: 10px;

            @media(max-width: 711px){
                grid-template-columns: auto;
                row-gap: 10px;
            }
        }

        .padded {
            padding: 0px 20px;

            h4{
                padding: 5px 0px ;
                margin: 20px 0px !important;
            }
        }

        &_section{
            margin-top: 20px;
        }

        button{
                width: 200px;
                margin-top: 20px;

                @media(max-width: 711px){
                    width: 100% !important;
                }
            
                svg{
                    font-size: 18px !important;
                }
            }
    }

    .message{
        margin: 5px 0px;
        background: rgba(7, 223, 7, 0.253);
        color: rgb(7, 44, 0);
        font-size: 16px;
        font-family: 'Regular';
        padding: 5px 10px;
    }
}
