.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 20px;
    border: none !important;


    input {
        opacity: 0;
        width: 0;
        height: 0;
        border: none !important;

        &:checked + .slider {
            background-color: #2196F3;
            &:before {
                -webkit-transform: translateX(30px);
                -ms-transform: translateX(30px);
                transform: translateX(30px);
              }
          }

          &:focus + .slider {
            box-shadow: 0 0 1px #2196F3;
          }
          
      }

    .slider {
        position: absolute;
        border-radius: 100px;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .2s;
        transition: .2s;

        

        &:before {
            position: absolute;
            content: "";
            height: 16px;
            width: 16px;
            left: 2px;
            bottom: 2px;
            background-color: white;
            -webkit-transition: .4s;
            transition: .4s;
            border-radius: 100px;
          }
        }
    
  }