@import '../../colors.scss';


.loader{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    .loading{
        width: 70px;
        display: block;
        height: 70px;
        border: 10px solid lightgray;
        border-right: 10px solid $standart;
        border-radius: 100px;
        animation: rotate .6s linear infinite;
    }

    @keyframes rotate{
        from{
            transform: rotate(0deg);
        }
        to{
            transform: rotate(360deg);
        }
    }
}